import React from "react";

import {
  Container,
  Row,
  Col,
  Card,
  CardBody,
  Form,
  Label,
  Input,
  Button,
  Modal,
  ModalHeader,
  ModalBody,
} from "reactstrap";

// import { Table } from "ant-table-extensions";
import { Table } from "antd";

import "./Attendance.css";

// core components
import SimpleHeader from "components/Headers/SimpleHeader.js";

import moment from "moment";

function Attendance() {
  const startOfMonth = moment().startOf("month").format("YYYY-MM-DD");
  const endOfMonth = moment().endOf("month").format("YYYY-MM-DD");
  // console.log("endOfMonth", endOfMonth);

  const daysOfMonth = Array.from(
    Array(moment("2020-02").daysInMonth()),
    (_, i) => i + 1 - 1
  );
  //   console.log(daysOfMonth.length);

  const [attendance, setAttendance] = React.useState({
    dateFrom: startOfMonth,
    dateTo: endOfMonth,
    name: "",
    studentId: "",
    selectClass: "",
    selectSection: "",
  });

  // console.log("attendance", attendance);

  const [atd, setAtd] = React.useState({});
  // console.log("atd", atd);

  const [modal, setModal] = React.useState(false);

  const toggle = () => setModal(!modal);

  const handleChange = (name) => (event) => {
    // formData.set(name, event.target.value);
    setAttendance({ ...attendance, [name]: event.target.value });
  };

  //   const handleOpen = (event) => {
  //     console.log("event", event.pageX);
  //     toggle();
  //   };

  //   function onChange(pagination, filters, sorter, extra) {
  //     console.log("params", pagination, filters, sorter, extra);
  //   }

  // function handleAttendance(key) {
  //   console.log("key", key);
  // }

  const columns = [
    {
      title: "#",
      width: 50,
      dataIndex: "hash",
      key: "hash",
      fixed: "left",
    },
    {
      title: "Name",
      width: 100,
      dataIndex: "name",
      key: "name",
      fixed: "left",
    },
  ];

  for (let i = 1; i < daysOfMonth.length; i++) {
    // console.log("i", i);
    columns.push({
      key: i,
      title: i,
      width: 110,
    });
  }

  const data = [];
  for (let i = 1; i < columns.length; i++) {
    data.push({
      key: i,
      hash: `${i}`,
      name: "Ajay",
    });
  }

  return (
    <div>
      <SimpleHeader name="Student" parentName="Attendance" />
      <Container className="mt--6 shadow-lg" fluid>
        <Form>
          <Card>
            <CardBody>
              <Row md="4" className="d-flex justify-content-center mb-4">
                <Col md="6">
                  <Label
                    className="form-control-label"
                    htmlFor="example-date-input"
                  >
                    From
                  </Label>
                  <Input
                    className="form-control-sm"
                    id="example-date-input"
                    type="date"
                    onChange={handleChange("dateFrom")}
                    value={attendance.dateFrom}
                    required
                  />
                </Col>
                <Col md="6">
                  <Label
                    className="form-control-label"
                    htmlFor="example-date-input"
                  >
                    To
                  </Label>
                  <Input
                    className="form-control-sm"
                    id="example-date-input"
                    type="date"
                    onChange={handleChange("dateTo")}
                    value={attendance.dateTo}
                  />
                </Col>
              </Row>
              <Row className="mt-4">
                <Col md="3">
                  <Input
                    className="form-control-sm"
                    id="example4cols2Input"
                    placeholder="Name"
                    onChange={handleChange("name")}
                    value={attendance.name}
                    type="text"
                    required
                  />
                </Col>
                <Col md="3">
                  <Input
                    className="form-control-sm"
                    id="example4cols2Input"
                    placeholder="StudentID"
                    type="text"
                    onChange={handleChange("studentId")}
                    value={attendance.studentId}
                    required
                    // value={startOfMonth}
                  />
                </Col>
                <Col md="3">
                  <Input
                    className="form-control-sm"
                    id="exampleFormControlSelect3"
                    type="select"
                    onChange={handleChange("selectClass")}
                    value={attendance.selectClass}
                    required
                  >
                    <option value="" disabled selected>
                      Select Class
                    </option>
                    <option>LKG</option>
                    <option>UKG</option>
                    <option>1st</option>
                    <option>2nd</option>
                    <option>3rd</option>
                    <option>4th</option>
                    <option>5th</option>
                    <option>6th</option>
                    <option>7th</option>
                    <option>8th</option>
                    <option>9th</option>
                    <option>10th</option>
                    <option>11th</option>
                    <option>12th</option>
                  </Input>
                </Col>
                <Col md="3">
                  <Input
                    className="form-control-sm"
                    id="exampleFormControlSelect3"
                    type="select"
                    onChange={handleChange("selectSection")}
                    value={attendance.selectSection}
                    required
                  >
                    <option value="" disabled selected>
                      Select Section
                    </option>
                    <option>A</option>
                    <option>B</option>
                    <option>C</option>
                    <option>D</option>
                  </Input>
                </Col>
                <Col md="3" className="mt-4">
                  <Button color="primary">Search</Button>
                </Col>
              </Row>
            </CardBody>
          </Card>
        </Form>
      </Container>

      <div className="">
        <div>
          <h3 className="start-end-date">
            {startOfMonth} - {endOfMonth}
          </h3>
        </div>

        <div className="attendance-icons">
          <h1
            className="ni ni-single-02"
            style={{ background: "green", color: "white" }}
          ></h1>
          <span className="tags"> - Present</span>
          <h1
            className="ni ni-single-02"
            style={{ background: "rgb(201, 3, 3)", color: "white" }}
          ></h1>
          <span> - Absent</span>

          <h1
            className="ni ni-single-02"
            style={{ background: "rgb(243, 243, 71)", color: "white" }}
          ></h1>
          <span> - Leave</span>

          <div className="attendance-button">
            <Button onClick={toggle} color="primary">
              Add Attendance
            </Button>
          </div>
        </div>
      </div>
      <Container className="mt--0 shadow-lg table-responsive" fluid>
        <Row>
          <Col>
            <Card>
              <CardBody>
                <Table
                  columns={columns}
                  dataSource={data}
                  scroll={{ x: 1500, y: 600 }}
                />
              </CardBody>
            </Card>
          </Col>
        </Row>
        <Modal isOpen={modal} toggle={toggle}>
          <ModalHeader isClose={modal} toggle={toggle}>
            Add Attendance
          </ModalHeader>
          <ModalBody className="custom-modal-style">
            {/* <div>
              {data.map((student) => {
                console.log("student", student.key);
                return (
                  <>
                    {" "}
                    <div className="studentAttendance">
                      <p key={student.key}>{student.name}</p>
                      <span>
                        <div>
                          <input
                            type="radio"
                            name={student.key}
                            value={atd}
                            onChange={(e) =>
                              // setAtd([e.target.name], e.target.value)
                              setAtd({
                                ...atd,
                                [e.target.name]: e.target.value,
                              })
                            }
                          />
                          Present
                          <input
                            type="radio"
                            name={student.key}
                            value={atd}
                            onChange={(e) =>
                              // setAtd([e.target.name], e.target.value)
                              setAtd({
                                ...atd,
                                [e.target.name]: e.target.value,
                              })
                            }
                          />{" "}
                          Absent
                          <input
                            type="radio"
                            name={student.key}
                            value={atd}
                            onChange={(e) =>
                              // setAtd([e.target.name], e.target.value)
                              setAtd({
                                ...atd,
                                [e.target.name]: e.target.value,
                              })
                            }
                          />{" "}
                          Leave
                        </div>
                      </span>
                    </div>
                  </>
                );
              })}
            </div> */}
          </ModalBody>
        </Modal>
      </Container>
    </div>
  );
}

export default Attendance;
