import React, { useState, useEffect } from 'react';
import { Container, CardHeader, Table, Button, Modal, Card, ModalFooter, ModalBody, Row, Col, Input, Form, CardBody } from 'reactstrap';
import { FaEdit } from 'react-icons/fa';
import SimpleHeader from 'components/Headers/SimpleHeader.js';
import './styles.css';
import { ToastContainer } from 'react-toastify';

const DepartmentList = () => {
  const [editing, setEditing] = useState(false);
  const [isActive, setIsActive] = useState(false);
  const [data, setData] = useState([]);
  const [editDepartmentName, setEditDepartmentName] = useState('');
  const [editDepartmentModule, setEditDepartmentModule] = useState('');

  useEffect(() => {
    const myData = [
      {
        name: 'Science',
        module: 'hurray',
      },
      {
        name: 'Science',
        module: 'hurray',
      },
      {
        name: 'Science',
        module: 'hurray',
      },
      {
        name: 'Science',
        module: 'hurray',
      },
      {
        name: 'Science',
        module: 'hurray',
      },
      {
        name: 'Science',
        module: 'hurray',
      },
      {
        name: 'Science',
        module: 'hurray',
      },
      {
        name: 'Science',
        module: 'hurray',
      },
      {
        name: 'Science',
        module: 'hurray',
      },
      {
        name: 'Science',
        module: 'hurray',
      },
      {
        name: 'Science',
        module: 'hurray',
      },
      {
        name: 'Science',
        module: 'hurray',
      },
      {
        name: 'Science',
        module: 'hurray',
      },
      {
        name: 'Science',
        module: 'hurray',
      },
      {
        name: 'Science',
        module: 'hurray',
      },
    ];
    setData(myData);
  }, []);

  function rowHandler(e, row) {
    e.stopPropagation();
    setIsActive(row);
    setEditDepartmentName(data[row - 1].name);
    setEditDepartmentModule(data[row - 1].module);
  }
  function handleEdit() {
    setData((dat) => {
      dat[isActive - 1].name = editDepartmentName;
      dat[isActive - 1].module = editDepartmentModule;
      return dat;
    });
    setEditing(false);
  }
  const [formData] = useState(new FormData());
  const handleChange = (name) => (event) => {
    formData.set(name, event.target.value);
  };
  const handleFormChange = async (e) => {
    e.preventDefault();
  };
  return (
    <>
      <SimpleHeader name="Department List" parentName="Department Management" />
      <ToastContainer position="bottom-right" autoClose={5000} hideProgressBar={false} newestOnTop={false} closeOnClick rtl={false} pauseOnFocusLoss draggable pauseOnHover theme="colored" />
      <Container className="mt--6" fluid>
        <Row>
          <Col lg="4">
            <div className="card-wrapper">
              <Card>
                <Form onSubmit={handleFormChange} className="mb-4">
                  <CardBody>
                    <Row>
                      <Col>
                        <label className="form-control-label" htmlFor="example4cols2Input">
                          Department Name
                        </label>
                        <Input id="example4cols2Input" placeholder="Department Name" type="text" onChange={handleChange('dept_name')} required />
                      </Col>
                    </Row>
                    <Row>
                      <Col>
                        <label className="form-control-label" htmlFor="example4cols2Input">
                          Module
                        </label>
                        <Input id="example4cols2Input" placeholder="Module" type="text" onChange={handleChange('module')} required />
                      </Col>
                    </Row>
                    <Row className="mt-4 float-right">
                      <Col>
                        <Button color="primary" type="submit">
                          Submit
                        </Button>
                      </Col>
                    </Row>
                  </CardBody>
                </Form>
              </Card>
            </div>
          </Col>
          <Col>
            <div className="card-wrapper">
              <Card>
                <CardHeader>
                  <Row>
                    <Col md="10"></Col>
                    <Col className="text-right">
                      <Button type="button" color="primary" onClick={() => setEditing(true)} disabled={isActive ? false : true}>
                        <FaEdit style={{ paddingRight: '5px' }} />
                        Edit
                      </Button>
                    </Col>
                  </Row>
                </CardHeader>
                <CardBody>
                  <Row className="ml-2">
                    <Table hover bordered onClick={() => true}>
                      <thead style={{ position: 'sticky' }}>
                        <tr>
                          <th>Department Name</th>
                          <th>Module</th>
                        </tr>
                      </thead>
                      <tbody>
                        {data?.map((d, i) => (
                          <tr className={isActive === i + 1 ? 'my-rows rowactive' : 'my-rows'} key={i} onClick={(e) => rowHandler(e, i + 1)}>
                            <td>{d.name}</td>
                            <td>{d.module}</td>
                          </tr>
                        ))}
                      </tbody>
                    </Table>
                  </Row>
                </CardBody>
              </Card>
            </div>
          </Col>
        </Row>
        <Modal className="modal-dialog-centered" isOpen={editing} toggle={() => setEditing(false)}>
          <div className="modal-header">
            <h2 className="modal-title" id="modal-title-default">
              {editing ? 'Edit Form' : 'Create Form'}
            </h2>
            <button aria-label="Close" className="close" data-dismiss="modal" type="button" onClick={() => setEditing(false)}>
              <span aria-hidden={true}>×</span>
            </button>
          </div>
          <ModalBody>
            <Row>
              <Col>
                <label className="form-control-label">Department Name</label>
                <Input id="form-department-name" value={editDepartmentName} onChange={(e) => setEditDepartmentName(e.target.value)} placeholder="School Address" type="text" />
              </Col>
            </Row>
            <Row>
              <Col>
                <label className="form-control-label">Module</label>
                <Input id="form-module-name" value={editDepartmentModule} onChange={(e) => setEditDepartmentModule(e.target.value)} placeholder="School Address" type="text" />
              </Col>
            </Row>
          </ModalBody>
          <ModalFooter>
            <Button color="success" type="button" onClick={handleEdit}>
              Save changes
            </Button>
          </ModalFooter>
        </Modal>
      </Container>
    </>
  );
};

export default DepartmentList;
